      display: none;
  }
  .fade-choose-enter, .fade-choose-leave-active {
      opacity: 0;
  }
  .toggle-cart-enter-active, .toggle-cart-leave-active {
      transition: all .3s ease-out;
  }
  .toggle-cart-enter, .toggle-cart-leave-active {
      transform: translateY(100%);
  }
</style>



